@keyframes fade {
    from { opacity: 1.0; }
    50% { opacity: 0.3; }
    to { opacity: 1.0; }
}                                                                                                                                                                                                                                  

@-webkit-keyframes fade {
    from { opacity: 1.0; }
    50% { opacity: 0.3; }
    to { opacity: 1.0; }
}

.user-info div{
    display: flex;
    align-items: center;
}
.user-info .bubble{
    animation: fade 2000ms infinite;
    margin-right: 4px;
    width: 8px;
    height: 8px;
    border-radius: 8px;
    background: inherit;
}

.user-info .connected .bubble{
    background-color: green;
}

.user-info .disconnected .bubble{
    background-color: red;
    animation: none;
}

.user-info .connected{
    color: green;
}

.user-info .disconnected{
    color: red;
}